.item1 { grid-area: 1 / 1 ; }
.item2 { grid-area: 1 / 2 ; }
.item3 { grid-area: 1 / 3 ; }
.item4 { grid-area: 1 / 4 ; }
.item5 { grid-area: 1 / 5 ; }
.item6 { grid-area: 1 / 6 ; }
.item7 { grid-area: 1 / 7 ; }

.grid-container {
  display: grid;
  grid-auto-rows: 150px;
  grid-gap: 10px;
  background-color: #2196F3;
  padding: 60px;
  flex-wrap: nowrap;
}

.grid-container > div {
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
  border-radius: 10%;
}
